<template>
  <div>
    <loading :active.sync="isLoading"></loading>
    <nav class="navbar navbar-light bg-primary">
      <router-link class="navbar-brand" to="/products">
        <i class="fas fa-user-tie" aria-hidden="true"></i>
         Gentleman Shop
      </router-link>
    </nav>
    <div class="container main-contant py-5">
      <h1 class="text-center mb-3 text-secondary"></h1>
      <section class="form-row align-items-center text-center">
        <div class="col">
          <div class="alert alert-light alert-rounded mb-0" role="alert">1.</div>
        </div>
        <div class="col">
          <div
            class="alert alert-rounded mb-0"
            role="alert"
            :class="{'alert-success':!order.is_paid,'alert-light':order.is_paid}"
          >2.</div>
        </div>
        <div class="col">
          <div
            class="alert alert-rounded mb-0"
            role="alert"
            :class="{'alert-success':order.is_paid,'alert-light':!order.is_paid}"
          >3.</div>
        </div>
      </section>
    </div>

    <div class="my-5 row justify-content-center">
      <form class="col-md-6" @submit.prevent="payOrder">
        <h2 class="text-center"></h2>
        <table class="table">
          <thead>
            <th></th>
            <th></th>
            <th></th>
          </thead>
          <tbody>
            <tr v-for="item in order.products" :key="item.id">
              <td class="align-middle">{{ item.product.title }}</td>
              <td class="align-middle">{{ item.qty }}/{{ item.product.unit }}</td>
              <td class="align-middle text-right">{{ item.final_total }}</td>
            </tr>
          </tbody>
          <tfoot>
            <tr>
              <td colspan="2" class="text-right"></td>
              <td class="text-right">{{ order.total }}</td>
            </tr>
          </tfoot>
        </table>

        <h2 class="text-center"></h2>
        <table class="table">
          <tbody>
            <tr>
              <th width="200">Email</th>
              <td>{{ order.user.email }}</td>
            </tr>
            <tr>
              <th></th>
              <td>{{ order.user.name }}</td>
            </tr>
            <tr>
              <th></th>
              <td>{{ order.user.tel }}</td>
            </tr>
            <tr>
              <th></th>
              <td>{{ order.user.address }}</td>
            </tr>
            <tr>
              <th></th>
              <td>
                <span v-if="!order.is_paid"></span>
                <span v-else class="text-success"></span>
              </td>
            </tr>
          </tbody>
        </table>
        <div class="text-right" v-if="!order.is_paid">
          <button class="btn btn-danger"></button>
        </div>
      </form>
      <div class="alert alert-secondary mt-4" role="alert" v-if="order.is_paid">
        <h2 class="text-center"></h2>
        <p></p>
        <br />
        <p>{{orderID}}</p>
        <br />
        <p></p>
        <br />
      </div>
    </div>
  </div>
</template>


<script>
export default {
  data() {
    return {
      isLoading: "",
      orderID: "",
      order: {
        user: {}
      }
    };
  },
  methods: {
    getOrder() {
      const vm = this;
      const api = `${process.env.APIPATH}/api/${process.env.COUSTOMPATH}/order/${vm.orderID}`;
      vm.isLoading = true;
      this.$http.get(api).then(response => {
        vm.order = response.data.order;
        vm.isLoading = false;
      });
    },
    payOrder() {
      const vm = this;
      const url = `${process.env.APIPATH}/api/${process.env.COUSTOMPATH}/pay/${vm.orderID}`;
      vm.isLoading = true;
      this.$http.post(url).then(response => {
        console.log(response);
        if (response.data.success) {
          console.log(response);
          vm.getOrder();
        }
        vm.isLoading = false;
      });
    }
  },
  created() {
    this.orderID = this.$route.params.orderId;
    this.getOrder();
  }
};
</script>


